@mixin bgImg($imgBase64) {
  background-image: url("#{$imgBase64}");
}

@mixin borderRadius($size) {
  -webkit-border-radius: $size;
  -moz-border-radius: $size;
  border-radius: $size;
}

.colorpicker-saturation {
  width: 100px;
  height: 100px;
  @include bgImg('../images/image7.png');
  cursor: crosshair;
  float: left;
  .colorpicker-guide {
    display: block;
    height: 5px;
    width: 5px;
    border: 1px solid #000;
    @include borderRadius(5px);
    position: absolute;
    top: 0;
    left: 0;
    margin: -4px 0 0 -4px;
    i {
      display: block;
      height: 5px;
      width: 5px;
      border: 1px solid #fff;
      @include borderRadius(5px);
    }
  }
}

.colorpicker-hue,
.colorpicker-alpha {
  width: 15px;
  height: 100px;
  float: left;
  cursor: row-resize;
  margin-left: 4px;
  margin-bottom: 4px;
}

.colorpicker-hue .colorpicker-guide,
.colorpicker-alpha .colorpicker-guide {
  display: block;
  height: 1px;
  background: #000;
  border-top: 1px solid #fff;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  margin-top: -1px;
}

.colorpicker-hue {
  @include bgImg('../images/image8.png');
}

.colorpicker-alpha {
  @include bgImg('../images/image9.png');
  background-position: 0 100%;
}

.colorpicker-preview div {
  position: relative;
  width: 100%;
  height: 100%;
}

.colorpicker-swatch {
  cursor: pointer;
  float: left;
  height: 12px;
  width: 12px;
}

.colorpicker-swatch + .colorpicker-swatch {
  margin-left: 2px;
}
